import * as React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import BackgroundImage from 'gatsby-background-image'

import * as THREE from "three";

import Layout from "../components/layout"
import FullScreenSection from "../components/full-screen-section"
import Seo from "../components/seo"

import styled from 'styled-components'

import { Suspense , useRef, useState, useMemo, useEffect } from 'react';

import { Environment, OrbitControls } from "@react-three/drei";
import { Canvas, useFrame, useThree } from '@react-three/fiber';

import { MTLLoader } from "three/examples/jsm/loaders/MTLLoader";
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader'
import { MeshSurfaceSampler } from "three/examples/jsm/math/MeshSurfaceSampler";
import { useLoader } from '@react-three/fiber'

// import cdl_muros from '../images/cdl_muros.obj';

function intersectionsFilter(intersections) {
  return intersections?.length ? [intersections[0]] : intersections
}


const RepositorioPage = () => {


const MurosScene = () => {

  const materials = useLoader(MTLLoader, "/untitled.mtl");
  const obj = useLoader(OBJLoader, '/untitled.obj', (loader) => {
    materials.preload();
    loader.setMaterials(materials);
  });

  const geometry = new THREE.BufferGeometry();

  var len = 64 * 64 * 4;
  var data = [];

  const sprite = new THREE.TextureLoader().load( '/disc.png' );

  let mesh = obj.children[1];

  mesh.scale.set(2000, 2000, 2000);

  const sampler = new MeshSurfaceSampler(mesh).build();
  for (let i = 0; i < len; i += 4) {

    const tempPosition = new THREE.Vector3();

    sampler.sample(tempPosition);

    // data.push(tempPosition);
    data[i] = tempPosition.x;

    data[i + 1] = tempPosition.y;

    data[i + 2] = tempPosition.z;

    data[i + 3] = Math.random() + 1;

  }

  geometry.setAttribute( 'position', new THREE.Float32BufferAttribute( data, 3 ) );

  // let material = new THREE.PointsMaterial( { size: 0.1, sizeAttenuation: true, map: sprite, alphaTest: 0.5, transparent: true } );
  let material = new THREE.PointsMaterial( { size: 0.1, color: 0xFFFFFF, sizeAttenuation: true, alphaTest: 0.5 } );
  // material.color.setHSL( 1.0, 0.3, 0.7 );

  const particles = new THREE.Points( geometry, material );

  // return <primitive object={obj} />;
  return <points geometry={geometry} material={material} ref={pointsRef} onPointerEnter={(e) => console.log("enter")}></points>
  }  

  const pointsRef = useRef();
  const raycaster = useRef();

  // useEffect( () => {
    // console.log(this);
  // }, [raycaster.intersectObjects])

  const data = useStaticQuery(
    graphql`
      query {
        bg1:

        file(relativePath: { eq: "pointcloud2.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

        bg2:
        file(relativePath: { eq: "obj.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

      }
    `
  )

  // const cdl_muros = React.lazy(() => import('../images/cdl_muros.obj'))
  

  // const obj = useLoader(OBJLoader, '../images/cdl_muros.obj')

  // const data2 = useStaticQuery(
  //   graphql`
  //     query {
  //       desktop: file(relativePath: { eq: "obj.jpg" }) {
  //         childImageSharp {
  //           fluid(quality: 90, maxWidth: 1920) {
  //             ...GatsbyImageSharpFluid_noBase64
  //           }
  //         }
  //       }
  //     }
  //   `
  // )

  const bg1 = data.bg1.childImageSharp.fluid
  const bg2 = data.bg2.childImageSharp.fluid

  return (

      <Layout>

        <Seo title="Home" />

        <FullScreenSection>

          <BackgroundImage
            Tag="section"
            className={'container-fluid fullheight-ribbon d-flex flex-column'}
            fluid={bg1}
            backgroundColor={`#040e18`}
          >

          	<div className={`row flex-1 pt-3`}>
        		        		
              <div className={`col h-100`}>

                <h1 className={`text-black display-4`}>Repositorio</h1>
                <p className={`text-black`}>Compendio de archivos de video, audio, fotografías y documentos que se relacionen con el mural del Paso Inferior Santa Lucía en un sólo lugar y que puedan servir para orientar a profesores e investigadores de artes visuales, arquitectura, urbanismo y público general con respecto al arte en espacio público.</p>

                <div className={`text-center`}>
                  <StaticImage
                    alt={`Curva de la Luz`}
                    src="../images/repositorio.png"
                    width={300}
                    quality={95}
                    style={{ 
                    }}
                  />                  
                </div>

                <div className={`mt-3 rounded-3`} style={{ position: "relative", width: '100%', height: 600, backgroundColor: 'rgba(0, 91, 172, 1)' }}>
                  <Canvas>
                    <Suspense fallback={null}>
                      <ambientLight intensity={0.1} />
                      <OrbitControls />
                      <MurosScene />
                      
                    </Suspense >  
                  </Canvas>
                </div>
                

              </div>

          	</div>

            <div className={`row ribbon no-gutters`}>

            	<div className={`col-3 ribbon-blue`}></div>
            	<div className={`col-2 ribbon-red`}></div>
            	<div className={`col-4 ribbon-navy`}></div>
            	<div className={`col-3 ribbon-gray`}></div>

            </div>

          </BackgroundImage>

        </FullScreenSection>
      
      </Layout>
    
  )

}

export default RepositorioPage;
